body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Abel', sans-serif;
}
body {
  background: #333;
  color: #fff;
}
.navigation-menu {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding:0;
  margin:0;
}
.navigation-menu li {
  list-style-type: none;
  width:120px;
  height:40px;
  line-height:40px;
  margin: 0 20px;
  text-align: center;
  text-transform: uppercase;
  background-color: rgb(68, 68, 68);
  border-radius: 5px;
}
.navigation-menu li:first-child {
  margin-left: 0;
}
.navigation-menu li:last-child {
  margin-right: 0;
}
.navigation-menu__link {
  color: rgb(255, 255, 255);
  text-decoration: none;
  transition: color 2s;
}
.navigation-menu__link:hover {
  color: rgb(42, 127, 255);
}

.react-page__project-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 500px;
  -webkit-align-content: center;
          align-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  background: #333;
  /* border: 1px solid red; */
}
.react-page__project {
  width: 400px;
  height: 400px;
  margin:20px;
  position:relative;
  font-size: 28px;
}

.react-page a:first-child .react-page__project {
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/reactpage/tictactoe.jpg') no-repeat;
  background-size: cover;
}
.react-page a:nth-child(2) .react-page__project{
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/reactpage/todo.jpg') no-repeat;
  background-size: cover;
}
.react-page a:last-child .react-page__project{
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/reactpage/forecast.jpg') no-repeat;
  background-size: cover;
}

.react-page .layer {
  display: none;
  background:rgba(0, 0, 0, .5);
  position:absolute;
  top: 0;
  left: 0;
  height:100%;
  width:100%;
}

.react-page a .react-page__project:hover .layer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.react-page__project a {
  color:white;
  margin:10px;
  text-decoration: none;
}
.react-page__project a:hover {
  color: tomato;
}

.homepage {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  height: 510px;
  padding:40px;
  background: url('https://cdn-images-1.medium.com/max/1600/1*dlN-vF1YJ7ZpOrx8rOGBEQ.jpeg') no-repeat;
  background-size: cover;
  margin-top:20px;
}
.homepage__photo {
  width:200px;
  height:200px;
  border-radius: 50%;
  background: url('https://raw.githubusercontent.com/Valeria38/portfolio/develop/src/img/homepage/me.jpg') no-repeat;
  background-size: cover;
  /* transition: background 3s; */
}
.homepage__photo:hover {
  /* background: rgba(0, 0, 0, .5); */
  /* background-size: cover; */
  /* background-position: 50% 50%; */
}

.homepage__info p {
  font-size:36px;
  color: rgb(255, 255, 255);
  margin-bottom:40px;
}


