@media screen and (max-width: 1450px) {
  .homepage {
    height:800px;
  }
  .react-page__project {
    width:300px;
    height: 300px;
    font-size: 26px;
  }
  .react-page__project-container {
    height: 400px;
  }
}

@media screen and (max-width: 1050px) {
  .react-page__project-container {
    height: 700px;
  }
  
  .homepage {
    height:1000px;
  }
}

@media screen and (max-width: 700px) {
  .navigation-menu li {
    /* font-size: 16px; */
    margin:0px;
    border-radius: 0;
  }
  .react-page__project-container {
    height: 1300px;
  }
  .react-page__project {
    width: 400px;
    height:400px;
    font-size: 28px;
  }
  .homepage {
    height: 1300px;
  }
}

